import React, { useState, useEffect } from 'react';
import Container from '@mui/material/Container';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import LoadingButton from '@mui/lab/LoadingButton';
import Typography from '@mui/material/Typography';
import { useSnackbar } from 'notistack';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';

import { useAuth } from "../../../context/authContext";
import API from "../../../config/api";

function Login() {
  const { enqueueSnackbar } = useSnackbar();
  let auth = useAuth();

  const [ennableToLogin, setEnnableToLogin] = useState(null);
  const [isLoadingLogin, setIsLoadingLogin] = useState(false);
  const [isLoadingRegister, setIsLoadingRegister] = useState(false);
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");


  useEffect(() => {
    enableLogin();
  }, []);

  const enableLogin = () => {
    API.post('/auth/ennablelogin', { })
      .then((response) => {
        if (response.data.enable) {
          setEnnableToLogin(true);
        } else {
          setEnnableToLogin(false);
        }
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  const handleLogin = () => {
    if (username.length > 0 && password.length > 0) {
      setIsLoadingLogin(true);
      API.post('/auth/login', { username, password })
        .then((response) => {
          auth.login({token: response.data.token, user: response.data.user});
        })
        .catch((error) => {
          enqueueSnackbar(error.response.data.message, { variant: 'error' });
        })
        .finally(() => {
          setIsLoadingLogin(false);
        });
    }
  }

  const handleRegister = () => {
    if (password.length < 5) {
      return enqueueSnackbar("Password must contains 5 or more letters", { variant: 'error' });
    }
    if (username.length === 0 || firstname.length === 0 || lastname.length === 0) {
      return enqueueSnackbar("All fields are required", { variant: 'error' });
    }
    setIsLoadingRegister(true);
    API.post('/auth/register', { username, firstname, lastname, password })
      .then((response) => {
        auth.login({token: response.data.token, user: response.data.user});
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      })
      .finally(() => {
        setIsLoadingRegister(false);
      });
  }

  return (
    <Container maxWidth="xl" sx={{ mt: 2 }}>
      {ennableToLogin &&
      <React.Fragment>
        <Typography variant="h5">Login</Typography>
        <Stack direction="column" spacing={1} sx={{ pt: 2 }}>
          <TextField fullWidth size="small" label="Username" sx={{ width: 400, maxWidth: '100%' }} autoComplete="off" value={username} onChange={(e) => setUsername(e.target.value)} />
          <TextField fullWidth size="small" label="Password" sx={{ width: 400, maxWidth: '100%' }} autoComplete="off" type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
          <LoadingButton fullWidth onClick={handleLogin} loading={isLoadingLogin} variant="outlined" sx={{ width: 400, maxWidth: '100%' }}>
            Login
          </LoadingButton> 
        </Stack>
      </React.Fragment>
      }
      {ennableToLogin === false &&
      <React.Fragment>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={12} md={4}>
            <Typography variant="h5">Register your account</Typography>
            <Stack direction="column" spacing={1} sx={{ pt: 2 }}>
              <TextField fullWidth size="small" label="Username" fullWidth autoComplete="off" value={username} onChange={(e) => setUsername(e.target.value)} />
              <TextField fullWidth size="small" label="First name" fullWidth autoComplete="off" value={firstname} onChange={(e) => setFirstname(e.target.value)} />
              <TextField fullWidth size="small" label="Last name" fullWidth autoComplete="off" value={lastname} onChange={(e) => setLastname(e.target.value)} />
              <TextField fullWidth size="small" label="Password" fullWidth autoComplete="off" type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
              <LoadingButton fullWidth onClick={handleRegister} loading={isLoadingRegister} variant="outlined" fullWidth>
                Register
              </LoadingButton>
            </Stack>
          </Grid>
          <Grid item xs={12} sm={12} md={4}>
            <Typography variant="subtitle1" sx={{ pt: 5 }}>This project created by Christos Apatsidis</Typography>
            <Typography variant="subtitle1">xristosapatsidis@yahoo.gr</Typography>
            <Typography variant="subtitle1">
              <Link href="https://github.com/ChristosApatsidis" underline="none" target="_blank">github.com</Link>
            </Typography>
          </Grid>
        </Grid>
      </React.Fragment>
      }
    </Container>
  );
}

export default Login;