import React, { useState, useEffect } from "react";
import API from "../../config/api";
import Cookies from "js-cookie";
import { useSnackbar } from 'notistack';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import IconButton from '@mui/material/IconButton';
import EditIcon from '@mui/icons-material/Edit';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import CircularProgress from '@mui/material/CircularProgress';
import LinearProgress from '@mui/material/LinearProgress';
import Collapse from '@mui/material/Collapse';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import MenuItem from '@mui/material/MenuItem';
import Grid from '@mui/material/Grid';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import SyncIcon from '@mui/icons-material/Sync';


function Home() {
  const [fetchDataLoading, setFetchDataLoading] = useState(true);
  return (
    <Container maxWidth="xl" sx={{ mt: 2 }}>
      {fetchDataLoading && <Box sx={{ display: 'flex', justifyContent: 'center'}}><CircularProgress /></Box>}  
      <Relays onLoad={(s) => setFetchDataLoading(s)} />
    </Container>
  );
}


function Relays(props) {
  const { enqueueSnackbar } = useSnackbar();

  const [updateBtnValueLoading, setUpdateBtnValueLoading] = useState(null);
  const [relays, setRelays] = useState([]);
  const [editRelayModal, setEditRelayModal] = useState({open: false, relay: null});

  useEffect(() => {
    getRelays();
  }, []);

  const getRelays = () => {
    props.onLoad(true);
    setRelays([]);
    // Get data from backend
    const token = Cookies.get("session");
    API.post('/relays', {}, { headers: { 'Authorization': token } })
      .then((response) => {
        setRelays(response.data.relays);
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      })
      .finally(() => {
        props.onLoad(false);
      });
  }

  const handleOnOffBtnClick = (data) => {
    if (!updateBtnValueLoading) {
      // Post data to backend and update value in state
      setUpdateBtnValueLoading(data._id);
      // Post
      const token = Cookies.get("session");
      API.post('/relays/open', { _id: data._id, Id: data.Id, open: data.open, HID: data.HID }, { headers: { 'Authorization': token } })
        .then((response) => {
          const relaysUpdated = relays.map(item => (item._id === data._id ? {...item, open: data.open} : item));
          setRelays(relaysUpdated);
        })
        .catch((error) => {
          enqueueSnackbar(error.response.data.message, { variant: 'error' });
        })
        .finally(() => {
          setTimeout(() => {
            setUpdateBtnValueLoading(null);
          }, 1000);
        });
    }
  }

  const handleOnOffBtnPress = (data) => {
    if (!updateBtnValueLoading) {
      // Post
      const token = Cookies.get("session");
      API.post('/relays/open', { _id: data._id, Id: data.Id, open: data.open, HID: data.HID }, { headers: { 'Authorization': token } })
        .then((response) => {
          const relaysUpdated = relays.map(item => (item._id === data._id ? {...item, open: data.open} : item));
          setRelays(relaysUpdated);
        })
        .catch((error) => {
          enqueueSnackbar(error.response.data.message, { variant: 'error' });
        });
    }
  }

  return (
    relays.length > 0 &&
      <React.Fragment>
        <Typography variant="h5">Relays</Typography>
        {relays.map((relay, index) => {
        return (
          <div style={{ paddingTop: 10 }} key={index}>
            <Card sx={{ minWidth: 275, maxWidth: 575 }}>
              <CardContent style={{ paddingBottom: 1 }}>
                <Typography variant="h5" component="div">
                  {relay.name}
                </Typography>
                <Typography variant="subtitle2" color="text.secondary">
                  RelayID: {relay.Id} | {relay.product}-{relay.serial}
                </Typography>
              </CardContent>
              <CardActions style={{ paddingTop: 5, paddingLeft: 16 }}>
                {relay.type === "On-Off" &&
                <ButtonGroup variant="outlined" aria-label="outlined button group">
                  <Button variant={relay.open ? "contained" : "outlined"} onClick={() => handleOnOffBtnClick({_id: relay._id, Id: relay.Id, open: true, type: relay.type, HID: relay.HID})}>On</Button>
                  <Button variant={relay.open ? "outlined" : "contained"} onClick={() => handleOnOffBtnClick({_id: relay._id, Id: relay.Id, open: false, type: relay.type, HID: relay.HID})}>Off</Button>                          
                </ButtonGroup>
                }
                {relay.type === "Click" &&
                  <Button variant={relay.open ? "contained" : "outlined"}
                    onMouseDown={() => handleOnOffBtnPress({_id: relay._id, Id: relay.Id, open: true, type: relay.type, HID: relay.HID})}
                    onMouseUp={() => handleOnOffBtnPress({_id: relay._id, Id: relay.Id, open: false, type: relay.type, HID: relay.HID})}
                    onTouchStart={() => handleOnOffBtnPress({_id: relay._id, Id: relay.Id, open: true, type: relay.type, HID: relay.HID})}
                    onTouchEnd={() => handleOnOffBtnPress({_id: relay._id, Id: relay.Id, open: false, type: relay.type, HID: relay.HID})}>
                      {relay.open ? "Open" : "Close"}
                  </Button>
                }
                <IconButton onClick={() => setEditRelayModal({open: true, relay: relay}) } aria-label="show more" style={{  marginLeft: 'auto' }}>
                  <EditIcon />
                </IconButton>
              </CardActions>
              <Collapse in={updateBtnValueLoading ? "true" : "false"}>
                {updateBtnValueLoading === relay._id &&<LinearProgress />}
              </Collapse>
            </Card>
          </div>
        );
      })}
      {editRelayModal.open &&
        <EditRelayModal open={editRelayModal.open} relay={editRelayModal.relay} onSave={() => getRelays()} onClose={() => setEditRelayModal({open: false, relay: null})} />
      }
    </React.Fragment>
  );
}


function EditRelayModal(props) {
  const { enqueueSnackbar } = useSnackbar();
  const relay = props.relay;

  const [relayProduct, setRelayProduct] = useState(null);
  const [relaySerial, setRelaySerial] = useState(null);
  const [relayHid, setRelayHid] = useState(null);
  const [relayId, setRelayId] = useState(null);
  const [relayName, setRelayName] = useState(null);
  const [relayType, setRelayType] = useState(null);

  useEffect(() => {
    setRelayProduct(relay.product);
    setRelaySerial(relay.serial);
    setRelayHid(relay.HID);
    setRelayId(relay.Id);
    setRelayName(relay.name);
    setRelayType(relay.type);
  }, []);

  const handleSave = () => {
    const token = Cookies.get("session");
    API.post('/relays/update', { _id: relay._id, name: relayName, type: relayType, Id: relayId, HID: relayHid }, { headers: { 'Authorization': token } })
      .then((response) => {
        props.onSave();
        props.onClose();
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  const handleDelete = () => {
    const token = Cookies.get("session");
    API.post('/relays/delete', { _id: relay._id }, { headers: { 'Authorization': token } })
      .then((response) => {
        props.onSave();
        props.onClose();
        enqueueSnackbar("Relay successfully deleted", { variant: 'success' });
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  const handleSyncPath = () => {
    const token = Cookies.get("session");
    API.post('/relays/sync', { _id: relay._id, product: relay.product, serial: relay.serial }, { headers: { 'Authorization': token } })
      .then((response) => {
        enqueueSnackbar("Sync complete", { variant: 'success' });
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  return (
    <React.Fragment>
      <Dialog {...props} fullWidth aria-labelledby="alert-dialog-title" aria-describedby="alert-dialog-description">
        <DialogTitle>{relayProduct}-{relaySerial}</DialogTitle>
        <DialogContent dividers>
          <DialogContentText>    
            <TextField label="Relay name" type="text" fullWidth variant="outlined" value={relayName} onChange={(e) => setRelayName(e.target.value)} size="small" sx={{ pb: 2 }} />  
            <Grid container spacing={1}>
              <Grid item xs={6}>
                <TextField select label="Relay type" value={relayType} onChange={(e) => setRelayType(e.target.value)} fullWidth size="small" sx={{ pb: 2 }}>
                  <MenuItem value="On-Off">On-Off</MenuItem>
                  <MenuItem value="Click">Click</MenuItem>
                </TextField>
              </Grid>
              <Grid item xs={6}>
                <TextField select label="Relay ID" value={relayId} onChange={(e) => setRelayId(e.target.value)} fullWidth size="small" sx={{ pb: 2 }}>
                  <MenuItem value={1}>1</MenuItem>
                  <MenuItem value={2}>2</MenuItem>
                  <MenuItem value={3}>3</MenuItem>
                  <MenuItem value={4}>4</MenuItem>
                  <MenuItem value={5}>5</MenuItem>
                  <MenuItem value={6}>6</MenuItem>
                  <MenuItem value={7}>7</MenuItem>
                  <MenuItem value={8}>8</MenuItem>
                </TextField>  
              </Grid>
            </Grid>
            <Grid container spacing={1}>
              <Grid item xs={12} md={6}>
                <Button variant="outlined" color="error" onClick={() => {if (window.confirm('Are you sure you delete this relay?')) handleDelete()}} fullWidth endIcon={<DeleteOutlineIcon />}>Delete</Button>
              </Grid>
              <Grid item xs={12} md={6}>
                <Button variant="outlined" color="success" onClick={handleSyncPath} fullWidth endIcon={<SyncIcon />}>Sync path</Button>
              </Grid>
            </Grid>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={props.onClose} variant="outlined">Cancel</Button>
          <Button onClick={handleSave} variant="outlined">Save</Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}

export default Home;