import React, { useState, useEffect } from "react";
import Cookies from "js-cookie";
import { useSnackbar } from 'notistack';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { useAuth } from "../../context/authContext";
import API from "../../config/api";

function ChangePassword() {
  const { enqueueSnackbar } = useSnackbar();
  let auth = useAuth();

  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [newPassword2, setNewPassword2] = useState("");

  const handleSave = () => {
    if (newPassword.length < 5) {
      enqueueSnackbar("Password must contains 5 or more letters", { variant: 'error' });
    }
    if (newPassword !== newPassword2) {
      enqueueSnackbar("New password fields must be same", { variant: 'error' });
    }
    if (newPassword === newPassword2 && newPassword.length > 5) {
      const token = Cookies.get("session");
      API.post('/auth/user/changepassword', { currentPassword, newPassword }, { headers: { 'Authorization': token } })
        .then((response) => {
          enqueueSnackbar("Password successfully updated", { variant: 'success' });
        })
        .catch((error) => {
          enqueueSnackbar(error.response.data.message, { variant: 'error' });
        });
    }
  }

  return (
    <Container maxWidth="xl" sx={{ mt: 2 }}>
      <Typography variant="h5">Change password</Typography>
      <Box sx={{ minWidth: 275, maxWidth: 475, pt: 1 }}>
        <TextField label="Current password" type="text" fullWidth variant="outlined" value={currentPassword} onChange={(e) => setCurrentPassword(e.target.value)} size="small" sx={{ pb: 2 }} />
        <TextField label="New password" type="text" fullWidth variant="outlined" value={newPassword} onChange={(e) => setNewPassword(e.target.value)} size="small" sx={{ pb: 2 }} />  
        <TextField label="New password" type="text" fullWidth variant="outlined" value={newPassword2} onChange={(e) => setNewPassword2(e.target.value)} size="small" sx={{ pb: 2 }} />   
        <Button onClick={handleSave} variant="outlined" fullWidth>Save</Button>
      </Box>
    </Container>
  );
}

export default ChangePassword;