import React, { useState, useEffect } from "react";
import Cookies from "js-cookie";
import { useSnackbar } from 'notistack';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { useAuth } from "../../context/authContext";
import API from "../../config/api";

function EditProfile() {
  const { enqueueSnackbar } = useSnackbar();
  let auth = useAuth();

  const [username, setUsername] = useState("");
  const [firstname, setFisrtname] = useState("");
  const [lastname, setLastname] = useState("");

  useEffect(() => {
    setUsername(auth.user.username);
    setFisrtname(auth.user.firstname);
    setLastname(auth.user.lastname);
  }, []);

  const handleSave = () => {
    const token = Cookies.get("session");
    API.post('/auth/user/edit', { username, firstname, lastname }, { headers: { 'Authorization': token } })
      .then((response) => {
        enqueueSnackbar("User successfully saved", { variant: 'success' });
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  return (
    <Container maxWidth="xl" sx={{ mt: 2 }}>
      <Typography variant="h5">Edit user profile</Typography>
      <Box sx={{ minWidth: 275, maxWidth: 475, pt: 1 }}>
        <TextField label="Username" type="text" fullWidth variant="outlined" value={username} onChange={(e) => setUsername(e.target.value)} size="small" sx={{ pb: 2 }} />
        <TextField label="First name" type="text" fullWidth variant="outlined" value={firstname} onChange={(e) => setFisrtname(e.target.value)} size="small" sx={{ pb: 2 }} />  
        <TextField label="Last name" type="text" fullWidth variant="outlined" value={lastname} onChange={(e) => setLastname(e.target.value)} size="small" sx={{ pb: 2 }} />   
        <Button onClick={handleSave} variant="outlined" fullWidth>Save</Button>
      </Box>
    </Container>
  );
}

export default EditProfile;