import React, { useState, useEffect } from 'react';
import API from "../../config/api";
import { useSnackbar } from 'notistack';
import Cookies from "js-cookie";
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import CircularProgress from '@mui/material/CircularProgress';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import MenuItem from '@mui/material/MenuItem';
import Grid from '@mui/material/Grid';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import LinearProgress from '@mui/material/LinearProgress';
import Collapse from '@mui/material/Collapse';
import Fade from '@mui/material/Fade';


function Settings() {
  return (
    <Container maxWidth="xl" sx={{ mt: 2}}>
      <Typography variant="h5">Settings</Typography>
      <RelaySettings /> 
    </Container>
  );
}

function RelaySettings(props) {
  const { enqueueSnackbar } = useSnackbar();

  const [connectedRelays, setConnectedRelays] = useState([]);
  const [relays, setRelays] = useState([]);
  const [connectedRelaysLoading, setConnectedRelaysLoading] = useState(false);
  const [newRelayModal, setNewRelayModal] = useState(false);
  const [newRelayName, setNewRelayName] = useState("");
  const [newRelayType, setNewRelayType] = useState("On-Off");
  const [newRelayId, setNewRelayId] = useState("");
  const [newRelayProduct, setNewRelayProduct] = useState("");
  const [newRelaySerial, setNewRelaySerial] = useState("");
  const [newRelayHid, setNewRelayHid] = useState("");
  const [step, setStep] = useState(1);

  useEffect(() => {
    getRelays();
    getConnectedRelays();
  }, []);

  const getConnectedRelays = () => {
    setConnectedRelaysLoading(true);
    // Get data from backend
    const token = Cookies.get("session");
    API.post('/relays/connected', {}, { headers: { 'Authorization': token } })
      .then((response) => {
        setConnectedRelays(response.data.relays);
        setConnectedRelaysLoading(false);
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  const getRelays = () => {
    setRelays([]);
    // Get data from backend
    const token = Cookies.get("session");
    API.post('/relays', {}, { headers: { 'Authorization': token } })
      .then((response) => {
        setRelays(response.data.relays);
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  const handleSyncPaths = () => {
    const token = Cookies.get("session");
    API.post('/relays/sync/all', {}, { headers: { 'Authorization': token } })
      .then((response) => {
        enqueueSnackbar("Sync complete", { variant: 'success' });
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  const newRelay = () => {
    const token = Cookies.get("session");
    API.post('/relays/new', { name: newRelayName, type: newRelayType, Id: newRelayId, product: newRelayProduct, serial: newRelaySerial, HID: newRelayHid }, { headers: { 'Authorization': token } })
      .then((response) => {
        enqueueSnackbar("Relay successfully added", { variant: 'success' });
        setNewRelayModal(false);
      })
      .catch((error) => {
        enqueueSnackbar(error.response.data.message, { variant: 'error' });
      });
  }

  const handleNewRelayModalFocus = () => {
    setStep(1);
    getConnectedRelays();
    setNewRelayName("");
    setNewRelayType("");
    setNewRelayId("");
    setNewRelayProduct("");
    setNewRelaySerial("");
    setNewRelayHid("");
    setNewRelayModal(true)
  }

  const handleBack = () => {
    setNewRelayHid("");
    setConnectedRelays([]);
    setStep(step-1)
    getConnectedRelays();
  }

  const handleSelectRelayModule = (relay) => {
    setNewRelayHid(relay.path);
    setNewRelayProduct(relay.product);
    setNewRelaySerial(relay.serial);
    setStep(2)
  }

  return (
    <React.Fragment>
      <Typography variant="h6">Relays settings</Typography>
      <Typography variant="subtitle1">Saved relays {relays.length}</Typography>
      <Typography variant="subtitle1">Connected modules {connectedRelays.length}</Typography>

      <Grid container spacing={1} md={4} sx={{ pt: 1 }}>
        <Grid item xs={12}>
          <Button onClick={handleSyncPaths} variant="outlined" fullWidth>Sync relays paths</Button>
        </Grid>
        <Grid item xs={12}>
          <Button onClick={handleNewRelayModalFocus} variant="outlined" fullWidth>New relay</Button>
        </Grid>
      </Grid>

      <Dialog open={newRelayModal} onClose={() => setNewRelayModal(false)} fullWidth aria-labelledby="alert-dialog-title" aria-describedby="alert-dialog-description">
        <DialogTitle>New relay</DialogTitle>
        <DialogContent dividers>
          <DialogContentText>
            {step === 1 &&
            <React.Fragment>
              {connectedRelaysLoading &&
                <LinearProgress />
              }
              {connectedRelays.length === 0 && !connectedRelaysLoading &&
                <Fade in={connectedRelays.length === 0 && !connectedRelaysLoading}>
                  <Typography variant="subtitle1">No ralay devices are connected</Typography>
                </Fade>
              }
              {connectedRelays.length > 0 &&
                <React.Fragment>
                  <Fade in={connectedRelays.length > 0}>
                    <Typography variant="subtitle1">Select device you want to use</Typography>
                  </Fade>
                  <Fade in={connectedRelays.length > 0}>
                    <List style={{ padding: 0, backgroundColor: '#212121' }}>
                    {connectedRelays.map((relay, index) => {
                      return (
                        <ListItem disablePadding key={index}>
                          <ListItemButton onClick={() => handleSelectRelayModule(relay)}>                       
                            <ListItemText primary={relay.product+"-"+relay.serial} />
                          </ListItemButton>
                        </ListItem>
                      )
                    })}
                    </List>
                  </Fade>
                </React.Fragment>
              }
            </React.Fragment>
            }
            {step === 2 &&   
            <React.Fragment>
              <TextField label="Relay name" autoComplete='off' type="text" fullWidth variant="outlined" value={newRelayName} onChange={(e) => setNewRelayName(e.target.value)} size="small" sx={{ pb: 2 }} />  
              <Grid container spacing={1}>
                <Grid item xs={6}>
                  <TextField select label="Relay type" value={newRelayType} onChange={(e) => setNewRelayType(e.target.value)} fullWidth size="small" sx={{ pb: 2 }}>
                    <MenuItem value="On-Off">On-Off</MenuItem>
                    <MenuItem value="Click">Click</MenuItem>
                  </TextField>
                </Grid>
                <Grid item xs={6}>
                  <TextField select label="Relay ID" value={newRelayId} onChange={(e) => setNewRelayId(e.target.value)} fullWidth size="small" sx={{ pb: 2 }}>
                    <MenuItem value={1}>1</MenuItem>
                    <MenuItem value={2}>2</MenuItem>
                    <MenuItem value={3}>3</MenuItem>
                    <MenuItem value={4}>4</MenuItem>
                    <MenuItem value={5}>5</MenuItem>
                    <MenuItem value={6}>6</MenuItem>
                    <MenuItem value={7}>7</MenuItem>
                    <MenuItem value={8}>8</MenuItem>
                  </TextField>  
                </Grid>
              </Grid>
              <Grid container spacing={1}>
                <Grid item xs={12} md={6}>
                  <TextField label="Relay product" type="text" fullWidth variant="outlined" value={newRelayProduct} onChange={(e) => setNewRelayProduct(e.target.value)} size="small" sx={{ pb: 1 }} />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField label="Relay serial" type="text" fullWidth variant="outlined" value={newRelaySerial} onChange={(e) => setNewRelaySerial(e.target.value)} size="small" />
                </Grid>
              </Grid>
            </React.Fragment>
            }
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setNewRelayModal(false)} variant="outlined">Cancel</Button>
          <div style={{flex: '1 0 0'}} />
          {step === 2 &&
            <React.Fragment>
              <Button onClick={() => handleBack()} variant="outlined">Back</Button>
              <Button onClick={newRelay} variant="outlined">Save</Button>
            </React.Fragment>
          }
        </DialogActions>
      </Dialog>

    </React.Fragment>
  );
}

export default Settings;