import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';

import { useAuth } from "../../context/authContext";

function Profile() {
  let navigate = useNavigate();
  let user = useAuth().user;

  return (
    <Container maxWidth="xl" sx={{ mt: 2}}>
      <Typography variant="h5">Hi {user.firstname} {user.lastname}</Typography>
      <Grid container spacing={1} md={4} sx={{ pt: 1 }}>
        <Grid item xs={12}>
          <Button onClick={() => navigate("/profile/edit")} variant="outlined" fullWidth>Edit profile</Button>
        </Grid>
        <Grid item xs={12}>
          <Button onClick={() => navigate("/profile/changepassword")} variant="outlined" fullWidth>Change password</Button>
        </Grid>
      </Grid>
    </Container>
  );
}

export default Profile;